.button-layout {
  display: flex;
  flex-direction: column;
  border-radius: 15%;
  cursor: pointer;
  padding: 10px 20px;
  transition: background-color 0.3s;
}

.button-layout:hover {
  background-color: #ececec;
}

mat-icon {
  display: block; /* 아이콘을 블록 요소로 만들어 세로 정렬 */
  margin: auto; /* 아이콘 가로 중앙 정렬 */
}
